.rev-form{
	margin-top: 10px;
	font-size: 14px;
	max-height: 0;
	padding: 0 30px;
	overflow: hidden;
	.transition(all .4s);
	&.active{
		max-height: 600px;
		padding: 26px 30px 30px;
	}
	@media @TabletPortrait{
		display: none;
	}
}
.rev-form__title{
	margin-top: 0;
	margin-bottom: 40px;
}
.rev-form__input{
	.placeholder({color: #999});
	border: none;
	border-bottom: 2px solid #E5E5E5;
	color: #333;
	width: 100%;
	line-height: 30px;
	.placeholder({color: #B8B8B8});
}
.rev-form__group{
	margin: 0 0 35px;
	padding: 0;
	border: none;
	& + &{
		margin-bottom: 23px;
	}
}
.rev-form__group-half{
	display: inline-block;
	width: 50%;
	&:first-child{
		padding-right: 10px;
	}
	&+&{
		padding-left: 10px;
	}
}
.rev-form__rating{
	margin: 0 0 35px;
}
.rev-form__stars{
	line-height: 0;
	vertical-align: middle;
	display: inline-block;
	margin-left: 1em;
	div{
		display: inline-block;
	}
}
.rev-form__text{
	height: 150px;
	width: 100%;
	resize: vertical;
	border: 2px solid #E5E5E5;
}
.rev-form__info{
	font-size: 12px;
	margin: 25px 0;
}
.rev-form__send{
	line-height: 26px;
}