#buy-popup{
	width: 580px;
	@media only screen and (max-width: 640px) {
		width: 100%;
	}
	&.active{
		padding: 30px 30px 40px;
	}
}
.order-popup{
	&__category{
		display: none;
		&.active{
			display: block;
		}
	}
	&__step1{
		display: none;
		&.active{
			display: block;
		}
	}
	&__title{
		font-size: 18px;
		line-height: 26px;
	}
	&__decription{
		margin-top: 20px;
		.clearfix();
		margin-bottom: 46px;
		&-image{
			width: 60px;
			float: left;
		}
		&-text{
			position: relative;
			display: block;
			padding-left: 68px;
			width: 100%;
			font-size: 14px;
			line-height: 19px;
			padding-top: 9px;
			padding-right: 20px;
			&>strong{
				margin-left: 4px;
			}
		}
	}
	&__img_manager{
		width: 49px;
		margin-top: 4px;
		margin-left: -5px;
	}
	&__buttons{
		.btn + .btn{
			margin-left: 20px;
		}
		@media @MobilePortrait {
			width: 100px;
			.btn{
				margin-bottom: 30px;
				& + .btn{
					margin-right: 0;
					margin-bottom: 0;
					margin-left: 0;
				}
			}
		}
	}
	&__congrat{
		display: none;
		text-align: center;
		margin-top: 8px;
		&.active{
			display: block;
		}
		&-image{
			width: 120px;
			margin: 0 auto;
			position: relative;
			height: 120px;
			border: 2px solid #babbb5;
			.border-radius(50%);
			&_opacity{
				border: none;
			}
			&>span{
				position: absolute;
				font-size: 8px;
				color: #e3e4e1;
				top: 58px;
				right: 29px;
				& + span{
					font-size: 14px;
					top: 46px;
					right: 17px;
					& + span{
						font-size: 20px;
						top: 33px;
						right: 1px;
					}
				}
			}
		}
		&-svg{
			position: absolute;
			top: -10px;
			left: -4px;
			display: block;
			width: 130px;
			height: 130px;
			fill: none;
			stroke-width: 4px;
			stroke: #ADB7BE;
			stroke-dasharray: 378;
			.transform(rotate(-90deg));
			z-index: 10;
			animation: preloader 2s linear infinite;
			stroke: #15b287;
			stroke-width: 2px;
			opacity: 1;
		}
		&-img{
			position: absolute;
			top: 50%;
			width: 50px;
			margin-top: -25px;
			margin-left: -25px;
			&_opacity{
				opacity: 0.5;
			}
		}
		&-title{
			margin-top: 20px;
			font-size: 18px;
			line-height: 32px;
		}
		&-name{

		}
		&-button{
			margin-top: 45px;
		}
		.order-popup__decription-text{
			padding-left: 0;
			padding-right: 0;
			padding-top: 16px;
		}
	}
	&__select-container{
		position: relative;
		display: block;
		padding-top: 12px;
		margin-bottom: 54px;
		.ui-selectmenu-button{
			height: 40px;
			margin-right: 15px;
		}
	}
	&__select-text{
		position: relative;
		font-size: 18px;
		font-weight: 300;
		padding-right: 10px;
		vertical-align: top;
		line-height: 27px;
		@media @MobilePortrait {
			display: block;
		}
	}
	.rev-form__group{
		margin: 0 0 21px;
		position: relative;
		& > &{

		}
		.rev-form__input{
			font-size: 18px;
			font-weight: 300;
			margin-bottom: 39px;
			line-height: 46px;
			&-val{
				display: none;
				position: absolute;
				top: 2px;
				right: 13px;
				width: 14px;
			}
			&.validate{
				border-bottom: 2px solid #15b287;
				& + .rev-form__input-val{
					display: block;
				}
			}
		}
		.rev-form__select{

		}
	}
}