.products-set{
	margin-top: 52px;
	position: relative;
	padding-bottom: 20px;
	&__title{
		margin: 0;
		margin-bottom: 35px;
		font-size: 18px;
	}
	
}
@media @TabletLandscape{
	.products-set{
		margin-top: 32px;
		padding-bottom: 0px;
	}
}
@media @TabletPortrait{
	.products-set{
		&__title{
			padding-left: 10px;
		}
	}
}

@media @MobileLandscape{
	.products-set{
		&__title{
			padding-left: 10px;
		}
	}
}

@media @MobilePortrait{
	.products-set{
		margin-top: 12px;
		&__title{
			width: 280px;
			text-align: left;
			margin: 0 auto;
			padding-left: 0;
			font-weight: 400;
			margin-bottom: 24px;
			display: block !important;
		}
	}
}