.price-slider{
	background: #fff;
	padding: 28px 20px 20px;
	position: relative;
	margin-bottom: 20px;
	&__slider{
		padding-top: 6px;
		padding-right: 20px;
		padding-bottom: 31px;
	}
	&__footer{
		position: relative;
	}
	&__text{
		vertical-align: top;
		display: inline-block;
		line-height: 32px;
		margin-right: 10px;
		font-size: 14px;
	}
	&__value{
		background: transparent;
		border: none;
		outline: none;
		border-bottom: 1px solid #cccccc;
		width: 60px;
		display: inline-block;
		vertical-align: top;
		height: 32px;
		margin-right: 14px;
		color: #334c5d;
		font-weight: normal;
		font-size: 14px;
		font-weight: 500;
	}
}