.prod-description{
	position: relative;
	color: #545454;
	
	font-size: 14px;
	.product__old-price{
		line-height: 22px;
	}
	.product__price {
		font-size: 24px;
		margin-bottom: 23px;
	}
	.wish-btn{
		margin-left: 15px;
	}
	@media @TabletLandscape{
		.wish-btn{
			margin-left: 0;
			float: right;
		}
	}
}
.prod-description__top{
	.btn{
		padding-left: 19px;
		&>.icon-cart{
			margin-right: 5px;
		}
	}
}
.product__old-price{
	
}
.product__price{
	
}
.prod-description__delimeter{
	margin: 30px 0 11px;
	border: none;
	border-top: 1px solid #E5E5E5;
}
.prod-description__table-container{
	position: relative;
	.clearfix();
	width: 100%;	
}
.prod-description__featured{
	width: 56%;
	display: table;
	margin: 20px 0 2px;
	table-layout: fixed;
	float: left;
	& + &{
		width: 44%;
		.prod-description__featured-col.icon{
			margin-top: -4px;
		}
		.prod-description__featured-col.text{
			padding-left: 4px;
		}
	}
	@media @TabletLandscape{
		width: 100%;
		margin: 9px 0;
		& + &{
			width: 100%;
		}
	}
	@media @TabletPortrait{
		width: 56%;
		margin: 20px 0 2px;
		& + &{
			width: 44%;
		}
	}
	@media @MobilePortrait{
		width: 100%;
		margin: 9px 0;
		& + &{
			width: 100%;
		}
	}

}

.prod-description__featured-col{
	display: table-cell;
	width: 50%;
	vertical-align: middle;
	font-size: 14px;

	&.icon{
		width: 35px;
		line-height: 0;
		@media @TabletLandscape{
			width: 40px;
		}
	}
	&.text{
		padding-left: 12px;
		line-height: 20px;
	}
}
.prod-description__list{
	list-style-type: none;
	margin: 0;
	padding: 0;
	column-count: 2;
	margin-bottom: 1.5em;
	font-size: 14px;
	padding-top: 14px;
	@media @TabletLandscape{
		column-count: 1;
	}
	@media @TabletPortrait{
		column-count: 2;
	}
	@media @MobilePortrait{
		column-count: 1;
	}
}
.prod-description__item{
	padding-left: 20px;
	position: relative;
	margin-bottom: 12px;
	&:before{
		content: '';
		color: inherit;
		display: inline-block;
		position: absolute;
		top: 4px;
		left: 0;
		width: 6px;
		height: 6px;
		border: 2px solid #B1BAC1;
		border-radius: 10px;
	}
}
.prod-description__text{
	line-height: 20px;
	margin-top: -2px;
	&>p{
		margin: 20px 0;
	}
}