.type-selector{
	background: #fff;
	padding: 28px 20px 20px;
	position: relative;
	margin-bottom: 20px;
	&__list{
		list-style: none;
		margin: 0;
		padding: 0;
		&_inlined{
			.clearfix();
			position: relative;
			vertical-align: top;
			display: block;
		}
	}
	&__item{
		& + .type-selector__item{
			margin-top: 20px;
		}
		&_color{
			float: left;
			width: 34px;
			height: 34px;
			margin-right: 5px;
			vertical-align: top;
			display: block;
			position: relative;
			margin-top: 0;
			margin-bottom: 5px;
			.cus-label{
				line-height: 0;
				font-size: 0;
				cursor: pointer;
			}
			& + .type-selector__item{
				margin-top: 0;
				margin-bottom: 5px;
			}
		}
	}
	&__title{
		margin: 0;
		line-height: 16px;
		font-size: 14px;
		margin-bottom: 22px;
		color: #585858;
		font-weight: normal;
		letter-spacing: 0em;
		font-weight: 500;
	}
	&__name{
		font-size: 14px;
		vertical-align: top;
	}
	&__chekbox-colored{
		position: relative;
		background: rgb(248,194,85); /* Old browsers */
		/* IE9 SVG, needs conditional override of 'filter' to 'none' */
		background: url(data:image/svg+xml;base64,);
		background: -moz-linear-gradient(left,  rgba(248,194,85,1) 0%, rgba(248,194,85,1) 50%, rgba(255,155,109,1) 51%, rgba(255,155,109,1) 100%); /* FF3.6+ */
		background: -webkit-gradient(linear, left top, right top, color-stop(0%,rgba(248,194,85,1)), color-stop(50%,rgba(248,194,85,1)), color-stop(51%,rgba(255,155,109,1)), color-stop(100%,rgba(255,155,109,1))); /* Chrome,Safari4+ */
		background: -webkit-linear-gradient(left,  rgba(248,194,85,1) 0%,rgba(248,194,85,1) 50%,rgba(255,155,109,1) 51%,rgba(255,155,109,1) 100%); /* Chrome10+,Safari5.1+ */
		background: -o-linear-gradient(left,  rgba(248,194,85,1) 0%,rgba(248,194,85,1) 50%,rgba(255,155,109,1) 51%,rgba(255,155,109,1) 100%); /* Opera 11.10+ */
		background: -ms-linear-gradient(left,  rgba(248,194,85,1) 0%,rgba(248,194,85,1) 50%,rgba(255,155,109,1) 51%,rgba(255,155,109,1) 100%); /* IE10+ */
		background: linear-gradient(to right,  rgba(248,194,85,1) 0%,rgba(248,194,85,1) 50%,rgba(255,155,109,1) 51%,rgba(255,155,109,1) 100%); /* W3C */
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f8c255', endColorstr='#ff9b6d',GradientType=1 ); /* IE6-8 */
		&:after{
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			display: block;
			height: 50%;
			width: 100%;
			background: rgb(191,231,158); /* Old browsers */
			/* IE9 SVG, needs conditional override of 'filter' to 'none' */
			background: url(data:image/svg+xml;base64,);
			background: -moz-linear-gradient(left,  rgba(191,231,158,1) 0%, rgba(191,231,158,1) 50%, rgba(125,213,201,1) 51%, rgba(125,213,201,1) 100%); /* FF3.6+ */
			background: -webkit-gradient(linear, left top, right top, color-stop(0%,rgba(191,231,158,1)), color-stop(50%,rgba(191,231,158,1)), color-stop(51%,rgba(125,213,201,1)), color-stop(100%,rgba(125,213,201,1))); /* Chrome,Safari4+ */
			background: -webkit-linear-gradient(left,  rgba(191,231,158,1) 0%,rgba(191,231,158,1) 50%,rgba(125,213,201,1) 51%,rgba(125,213,201,1) 100%); /* Chrome10+,Safari5.1+ */
			background: -o-linear-gradient(left,  rgba(191,231,158,1) 0%,rgba(191,231,158,1) 50%,rgba(125,213,201,1) 51%,rgba(125,213,201,1) 100%); /* Opera 11.10+ */
			background: -ms-linear-gradient(left,  rgba(191,231,158,1) 0%,rgba(191,231,158,1) 50%,rgba(125,213,201,1) 51%,rgba(125,213,201,1) 100%); /* IE10+ */
			background: linear-gradient(to right,  rgba(191,231,158,1) 0%,rgba(191,231,158,1) 50%,rgba(125,213,201,1) 51%,rgba(125,213,201,1) 100%); /* W3C */
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#bfe79e', endColorstr='#7dd5c9',GradientType=1 ); /* IE6-8 */
		}
	}
}