.catalog{
	&__heading{
		margin: 0 0 20px;
		height: 30px;
		line-height: 26px;
		&_bottom{
			display: none;
		}
	}
	&__touch-filter{
		display: none;
	}
	&__button-back{
		height: auto !important;
		&.mobile-menu__btn-back{
			display: none;
		}
		.mobile-menu__btn-content{
			position: relative;
			height: auto;
			width: 100%;
			padding: 34px 56px 27px;
			font-weight: 400;
			display: none;
			cursor: pointer;
			font-size: 14px;
		}
	}
	&__header{
		font-size: 14px;
		line-height: 30px;
		margin-bottom: 20px;
		text-align: right;
		
		.btn{
			margin-left: 16px;
			& + .btn{
				margin-left: 11px;
			}
		}
	}
	&__header-title{
		font-size: 14px;
		line-height: 29px;
		vertical-align: top;
		color: #545454;
	}
	&__filter{
		&>.shadow-box{
			display: none;
			left: auto;
			right: 0;
			&:after{
				background: #e9eae2;
			}
		}
	}
	&__footer{
		margin-top: 18px;
		margin-bottom: 91px;
	}
	&__total{
		margin-right: 140px;
		font-size: 14px;
	}
}
.catalog__list{
	margin: 0;
	padding: 0;
	list-style: none;
	overflow: hidden;
}
.catalog__item{
	float: left;
	width: (100/3)*1%;
	margin-bottom: 20px;
	&:nth-child(3n){
		padding-left: 10px;
	}
	&:nth-child(3n-1){
		padding: 0 10px;
	}
	&:nth-child(3n-2){
		padding-right: 10px;
	}
}
@media @TabletLandscape{
	.catalog{
		&__footer{
			text-align: center;
			margin-bottom: 40px;
			&>.btn{
				margin: 0;
			}
		}
		&__total{
			width: auto;
			display: block;
			margin-bottom: 30px;
			margin-right: 0;
		}
	}
}

