	.catalog{
		&__heading{
		}
		&__header{
			text-align: left;
			padding-left: 10px;
			.btn{
				margin-left: 0;
				& + .btn{
				}
			}
		}
		&__header-title{
			width: 100%;
			display: block;
		}
	}
	.catalog__list{
	}
