@media @MobilePortrait{
	.catalog{
		&__content{
			padding-top: 0;
			overflow: hidden;
		}
		&__heading{
			display: none;
			&_bottom{
				padding-top: 30px;
				display: block;
				padding-left: 30px;
				height: auto;
				margin-bottom: 23px;
			}
		}
		
		&__row{
			overflow: hidden;
		}
		&__footer{
			margin-bottom: 91px;
		}
		&__total{
			margin-bottom: 10px;
		}
		&__header{
			margin-top: 21px;
			padding-left: 30px;
			margin-bottom: 31px;
			&-title{
				margin-bottom: 10px;
			}
			.btn{
				& + .btn{
				}
			}
		}
		&__touch-filter{
			margin-left: 30px;
			display: inline-block;
		}
		&__header-title{
		}
		&__row{
			overflow: visible;
		}
		&__heading{
			&_bottom{
				padding-left: 0;
			}
		}
		&__button-back{
			&.mobile-menu__btn-back{
				display: block;
				.mobile-menu__btn-content{
					padding-left: 20px;
				}
				.steps__item-arrow{
					right: 118px;
					top: 58%;
				}
			}
			
		}
		&__header{
			padding-left: 0;
			.btn{
				& + &{
				}
			}
		}
		&__filter{
			position: absolute;
			margin: 0;
			top:0;
			left: -300px;
			margin-left: -20px !important;
			height: 100%;
			.transition(left .4s);
			&.active{
				left: 0;
			}
			&>.shadow-box{
				display: block;
			}
		}
		&__filterContainer{
			position: relative;
			margin-bottom: 0 !important;
			.transition(margin-left .4s);
			&.active{
				margin-left: 280px !important;
			}
		}
		&__touch-filter{
			margin-left: 0;
		}
		&__header-title{
		}
		&__footer{
		}
		&__total{
		}
	}
	.catalog__list{
	}
	.catalog__item{
		&:nth-child(3n){
		}
		&:nth-child(3n-1){
		}
		&:nth-child(3n-2){
		}
	}
}