.photoview{
	margin-bottom: 20px;
	@media @TabletPortrait{
		margin-bottom: 0;
	}
}
.photoview__title{
	.product__pb-title();
}
.photoview__gallery{
	position: relative;
	padding-bottom: 101%;
	@media @MobilePortrait{
		padding-bottom: 242%;
	}
}
.photoview__current{
	position: absolute;
	border: none;
	background: none;
	padding: 0;
	text-align: center;
	margin: 0 -30px 10px;
	display: none;
	top: 0;
	left: 0;
	&.active{
		display: block;
	}
	@media @MobileLandscape{
		width: 1000px;
		margin-left: -500px;
		left: 50%;
	}
}
.photoview__expand{
	position: absolute;
	top: 23px;
	left: 30px;
	line-height: 20px;
	opacity: .4;
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=40)";
	color: #374147;
	transition: opacity .2s ease-in-out;
	height: 500px;
	@media @MobilePortrait{
		display: none;
	}
	span{
		font-size: 14px;
		vertical-align: middle;
	}
	.photoview__current:hover &{
		opacity: 0.5;
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
	}
}
.photoview__expand-svg{
	vertical-align: middle;
	margin-right: 20px;
	fill: currentColor;
	width: 30px;
	height: 30px;
}
.photoview__cur-img{
	display: table-cell;
	vertical-align: middle;
	@media @MobileLandscape{
		display: inline-block;
		vertical-align: middle;
		max-width: 100%;
	}
}
.photoview__list{
	list-style: none;
	margin: 0 0 -10px;
	padding: 0;
	text-align: center;
	font-size: 0;
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	@media @MobileLandscape{
		margin: 0;
	}
}
.photoview__item{
	display: inline-block;
	vertical-align: top;
	position: relative;
	&.active{
		.photoview__link{
			&:after{
				content: '';
				position: absolute;
				background: #334c5d;
				top: 10px;
				left: 10px;
				width: 60px;
				height: 60px;
				opacity: 0.5;
				@media @MobileLandscape{
					width: 40px;
					height: 40px;
				}
			}
		}
		.photoview__svg{
			animation: loader-show 10s;
			stroke: #F8C255;
			stroke-width: 2px;
			opacity: 1;
		}
	}
}
.photoview__link{
	display: block;
	font-size: 16px;
	line-height: 0;
	padding: 10px;
	width: 80px;
	position: relative;
	height: 80px;
	@media @MobileLandscape{
		width: 55px;
		height: 60px;
	}
}
.photoview__img{
	display: block;
	width: 60px;
	height: 60px;
	position: relative;
	@media @MobileLandscape{
		width: 40px;
		height: 40px;
	}
}
.photoview__svg{
	position: absolute;
	top: 50%;
	margin-top: -12px;
	margin-left: -12px;
	left: 50%;
	opacity: 0;
	display: block;
	width: 24px;
	height: 24px;
	fill: none;
	stroke-width: 4px;
	stroke: #ADB7BE;
	stroke-dasharray: 62;
	z-index: 10;
	@media @MobileLandscape{
		margin-left: -10px;
	}
}
