.form {
	z-index: 10;
	.clearfix();
	display: inline-block;
	&_right {
		float: right;
		position: relative;
	}
	&.expand{
		.field{
			width: 420px;
		}
		.input_search{
			border-bottom: solid 2px rgba(21, 178, 135, 1);
		}
		.autocomplete{
			display: block;
		}
		@media @TabletLandscape{
			& + .site-header__info{
				.site-header__info-list{
					.site-header__info-item + .site-header__info-item{
						display: none;
					}
				}
			}
		}
	}
}