.prod-reviews{
	font-size: 14px;
	&__bottom{
		display: none;
	}
	
}
@media @TabletPortrait{
	.prod-reviews{
		display: none;
		&__bottom{
			display: block;
		}
	}
}
.prod-reviews__title{
	.product__pb-title();
	font-size: 18px;
	margin-top: 0;
	line-height: 12px;
	margin-bottom: 28px;
}
.prod-reviews__total{
	margin-bottom: 44px;
}
.prod-reviews__total-title{
	font-weight: 400;
	margin-bottom: 10px;
}
.prod-reviews__stars{
	margin-bottom: 10px;
	.stars{
		display: inline;
	}
	.count{
		font-weight: 500;
		margin-left: 0.7em;
	}
	.voices{}
}
.prod-reviews__list{
	list-style: none;
	margin: 0;
	padding: 0;
	margin-bottom: 29px;
}
.prod-reviews__item{
	border-top: 1px solid #E5E5E5;
	padding: 27px 0;
}
.prod-reviews__person{
	font-weight: 500;
	color: #333;
	margin-bottom: 10px;
}
.prod-reviews__text{
	line-height: 20px;
	margin-bottom: 10px;
}
.prod-reviews__date{
	color: #6E7C86;
	font-size: 12px;
}
.prod-reviews__toolbox{
	margin-bottom: 10px;
	button + button{
		margin-left: 20px;
	}

}
.prod-reviews__more-btn{
	svg{
		fill: currentColor;
	}
	span{
		
	}
}
.prod-reviews__form{}