.breaking-message {
	height: 110px;
	position: relative;
	margin-top: 40px;
	overflow: hidden;
	z-index: 1;
	background: #000;
	&__overlay {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: #334c5d;
		background: url('../img/breaking-bg.jpg');
		background-size: cover;
		background-position: 0 0;
		z-index: 1;
	}
	&__inner {
		display: table;
		width: 100%;
		height: 100%;
	}
	&__text {
		font-size: 18px;
		color: #fff;
		line-height: 20px;
		a {
			color: #fff;
			transition: color .3s;
			&:hover {
				color: #15b287;
			}
		}
	}
	&__cell {
		vertical-align: middle;
		display: table-cell;
	}
	.container {
		position: relative;
		height: 100%;
		z-index: 5;
	}
	.btn {
		margin: 0 60px;
	}
}
@media @TabletLandscape{
	.breaking-message {
		margin-top: 20px;
		&__overlay {
		}
		&__inner {
		}
		&__text {
			padding: 0 18px;
			a {
				&:hover {
				}
			}
		}
		&__cell {
		}
		.container {
		}
		.btn {
			margin: 0 24px;
		}
	}
}