.catalog-info{
	position: relative;
	text-align: center;
	&__container{
		max-width: 1180px;
		margin: 0 auto;
		background: #fff;
		margin: 0 auto !important;
		@media @TabletLandscape{
			max-width: 882px;
		}
	}
}
.catalog-description{
	position: relative;
	text-align: left;
	width: 100%;
	display: inline-block;
	&__icon-cell{
		float: left;
		width: 69px;
		display: block;
		padding-top: 28px;
		text-align: center;
	}
	&__icon{
		max-width: 32px;
	}
	&__title{
		position: relative;
		display: block;
		width: 100%;
		padding: 24px 20px 26px;
		padding-left: 70px;
		color: #babbb5;
		font-size: 12px;
		line-height: 20px;
		letter-spacing: -0.01em;
	}
}
.catalog{
	footer.site-footer{
		.content{
			padding-top: 0;
		}
	}
}