.main-menu {
	margin-top: 88px;
	padding-bottom: 240px;
	width: 100%;
	&__title {
		.roboto();
		font-size: 24px;
		font-weight: 100;
		color: #fff;
		margin: 0;
		margin-bottom: 24px;
		letter-spacing: -0.005em;
	}
	&__container{
		position: relative;
		z-index: 6;
	}
	&__list {
		.clearfix();
		list-style: none;
		padding: 0;
		margin: 0;
		display: table;
		width: 100%;
	}
	&__item {
		display: table-cell;
		background: #15B287;
		vertical-align: middle;
		height: 50px;
		transition: background .3s;
		&_dj {
			width: 116px;
			.main-menu__img {
				margin-left: 30px;
				margin-right: 10px;
			}
		}
		&_gt {
			width: 120px;
			.main-menu__img {
				margin-left: 12px;
				margin-right: 10px;
			}
		}
		&_dh {
			width: 144px;
			.main-menu__img {
				margin-left: 25px;
				margin-right: 8px;
			}
		}
		&_pi {
			width: 154px;
			.main-menu__img {
				margin-left: 20px;
				margin-right: 10px;
			}
		}
		&_sm {
			width: 160px;
			.main-menu__img {
				margin-left: 26px;
				margin-right: 10px;
			}
		}
		&_ud {
			width: 144px;
			.main-menu__img {
				margin-left: 26px;
				margin-right: 10px;
			}
		}
		&_zv {
			width: 110px;
			.main-menu__img {
				margin-left: 17px;
				margin-right: 4px;
			}
		}
		&_et {
			width: 100px;
			.main-menu__img {
				margin-left: 12px;
				margin-right: 10px;
			}
		}
		&_sv {
			.main-menu__img {
				margin-left: 22px;
				margin-right: 10px;
			}
		}
	}
	&__link {
		display: block;
		text-decoration: none;
		height: 50px;
		padding: 10px 0;
		position: relative;
		&:after{
			content: '';
			width: 0px;
			border: 10px solid transparent;
			position: absolute;
			bottom: -10px;
			left: 50%;
			margin-left: -10px;
			border-bottom: 0px solid #fff;
			opacity: 0;
			.transition(all .3s);
		}
		&:hover {
			text-decoration: none;
			background: #00a177;
		}
		&.active{
			background: #00a177;
			&:after{
				opacity: 1;
				border-width: 10px;
			}
		}

	}
	&__img {
		display: inline-block;
		width: 30px;
		margin: 0;
		vertical-align: middle;
	}
	&__text {
		display: inline-block;
		font-size: 16px;
		color: #fff;
		margin-right: 10px;
		letter-spacing: -0.01em;
		position: relative;
		.sans();
		&_short{
			display: none;
		}
		&_full{
			display: inline-block;
		}
		@media @TabletPortrait{
			font-size: 13px;
		}
		@media @TabletLandscape{
			&_short{
				display: inline-block;
			}
			&_full{
				display: none;
			}
		}
	}
	
}
