.input {
	display: inline-block;
	background: rgba(255,255,255,0.5);
	border: none;
	height: 50px;
	vertical-align: middle;
	font-size: 14px;
	color: #fff;
	width: 100%;
	padding: 0 20px;
	border-bottom: solid 2px rgba(255,255,255,0);
	transition: border .3s;
	-webkit-appearance: none;
	.border-radius(0);
	&_search{
		padding-right: 60px;

	}
	&:focus {
		border-color: rgba(255,255,255,0.7);
	}
	&:hover {
		border-color: rgba(255,255,255,1);
	}
}

.cus-checkbox{
	width: 0;
	height: 0;
	opacity: 0;
	visibility: hidden;
}
.checkbox-icon{
	vertical-align: top;
	width: 20px;
	height: 20px;
	position: relative;
	display: inline-block;
	font-size: 0;
	background-image: url('data:image/svg+xml;base64,');
	background-image:  none,url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAUCAYAAACNiR0NAAAABHNCSVQICAgIfAhkiAAAAAlwSFlzAAAOxAAADsQBlSsOGwAAAHBJREFUOI3t1cEJwzAQRNGZ4BrShyGgEnJOEerJpbiGZQRuQ7gHTU6B3Nf4pF/Au35K+tjeSD6RyPYJoC62tzHGu5RyZMCIWEnukOQM9J8kP67Cfk1wghOc4E2g7TMi1izUWnsB6AuASnKXlF1AJ1m/GYQvGINLjqsAAAAASUVORK5CYII=');
	background-size: 100% 100%;
	margin-right: 10px;
	overflow: hidden;
}
.checkbox-icon-color{
	position: relative;
	width: 34px;
	height: 34px;
	border: 2px solid transparent;
	.border-radius(3px);
	display: block;

}
.checkbox-icon-inside{
	position: relative;
	width: 20px;
	height: 20px;
	margin: 5px;
	display: block;
}
.cus-label{
	position: relative;
	.user-select(none);
	display: block;
	cursor: pointer;
	&:hover{
		.checkbox-icon{
			background-image: url('data:image/svg+xml;base64,');
			background-image:  none,url('data:image/png;base64,');
		}
		.cus-checkbox:checked + .checkbox-icon{
			background-image: url('data:image/svg+xml;base64,');
			background-image:  none,url('data:image/png;base64,');
		}
		.checkbox-icon-color{
			border-color: rgba(51, 76, 93, 0.5);
		}
	}
}
.cus-checkbox:checked + .checkbox-icon{
	background-image: url('data:image/svg+xml;base64,');
	background-image:  none,url('data:image/png;base64,');
	& + .checkbox-name{
		color: #334c5d;
	}
}

.cus-checkbox:checked + .checkbox-icon-color{
	border-color: #7f7f7f;
}

.checkbox-name{
	cursor: pointer;
}

.ie-8 .input{
	line-height: 50px;
	background: #ccc;
}
::-webkit-input-placeholder { /* WebKit browsers */
    color:    #fff;
}
:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
   color:    #fff;
   opacity:  1;
   -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
}
::-moz-placeholder { /* Mozilla Firefox 19+ */
   color:    #fff;
   opacity:  1;
   -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
}
:-ms-input-placeholder { /* Internet Explorer 10+ */
   color:    #fff;
}