.mobile-menu{
	position: absolute;
	left: 0;
	top: 0;
	width: 240px;
	opacity: 0;
	background: #293d4a;
	.transition(left .4s);
	display: none;
	height: 100%;
	letter-spacing: -0.054em;
	color: #545454;
	z-index: 1;
	&.active{
		opacity: 1;
	}
	@media @MobilePortrait{
		display: block;
	}
	&__container{
		display: block;
		background: #ffffff;
	}
	&__btn-back{
		display: block;
		height: 48px;
		background: #e9eae2;
		position: relative;
		&.active{
			.mobile-menu__btn-content{
				display: block;
			}
		}
	}
	&__btn-content{
		position: relative;
		height: 100%;
		width: 100%;
		padding: 15px 56px;
		font-weight: 400;
		display: none;
		cursor: pointer;
	}
	&__btn-icon{
		right: auto;
		left: 19px;
		transform: rotate(180deg);
	}
	&__title{
		display: block;
		height: 50px;
		padding: 15px 0 14px 26px;
		color: #fff;
		background: #15b287;
		overflow: hidden;
		&.disabled{
			height: 0;
			padding: 0;
		}
	}
	&__img{
		position: relative;
		height: 24px;
		width: 24px;
		display: inline-block;
		vertical-align: middle;
		margin-right: 10px;
		float: left;
	}
	&__main-list{
		padding: 0;
		list-style-type: none;
		margin: 0;
		position: relative;
	}
	&__main-item{
		display: block;
		height: auto;
		padding: 15px 50px 15px 28px;
		position: relative;
		background: #fff;
		opacity: 1;
		overflow: hidden;
		cursor: pointer;
		.clearfix();
		& + .mobile-menu__main-item{
			border-top: 2px solid #e9eae2;
		}
		.mobile-menu__img{
			display: none;
		}
		&.disabled{
			height: 0;
			padding: 0;
			overflow: hidden;
			border: none;
			opacity: 0;
		}
		&.active{
			background: #15b287;
			color: #fff;
			overflow: visible;
			.mobile-menu__img{
				display: inline-block;
			}
			&>.steps__item-arrow {
				.transform(rotate(90deg), scale(0.8));
				background: url('../img/icons/big_arrow-white.svg');
				width: 40px;
				height: 40px;
				margin-top: -22px;
			}
		}
		.steps__item-arrow {
			right: 16px;
			margin-top: -17px;
		}
	}
	&__second-list{
		padding: 0;
		list-style-type: none;
		margin: 0;
		max-height: 0;
		position: absolute;
		top: 50px;
		left: 0;
		width: 100%;
	}
	&__second-item{
		position: relative;
		display: block;
		height: auto;
		padding: 15px 50px 15px 28px;
		position: relative;
		background: #fff;
		color: #343434;
		overflow: hidden;
		cursor: pointer;
		.clearfix();
		& + .mobile-menu__second-item{
			border-top: 2px solid #e9eae2;
		}
		&.disabled{
			height: 0;
			padding: 0;
			overflow: hidden;
			border: none;
			opacity: 0;
		}
		&.active{
			background: #334c5d;
			color: #fff;
			overflow: visible;
			border-top:none;
			&>.steps__item-arrow {
				.transform(rotate(90deg), scale(0.8));
				background: url('../img/icons/big_arrow-white.svg');
				width: 40px;
				height: 40px;
				margin-top: -22px;
			}
		}
	}
	&__third-list{
		padding: 0;
		list-style-type: none;
		margin: 0;
		max-height: 0;
		position: absolute;
		top: 100%;
		left: 0;
		width: 100%;
	}
	&__third-item{
		position: relative;
		display: block;
		height: auto;
		padding: 0;
		position: relative;
		background: #fff;
		color: #343434;
		cursor: pointer;
		.clearfix();
		& + .mobile-menu__third-item{
			border-top: 2px solid #e9eae2;
		}
		&.hover{
			background: #334c5d;
			color: #fff;
		}
	}
	&__cat-title{
		position: relative;
		display: block;
		line-height: 20px;
		float: left;
	}
	&__link{
		position: relative;
		height: auto;
		width: 100%;
		padding: 15px 28px 15px 28px;
		display: block;
		&>span{
			line-height: 20px;
			vertical-align: middle;
			color: #343434;
			font-size: 14px;
			display: inline-block;
		}
	}
	&__icon{
		padding: 0;
		margin: 0;
		list-style-type: none;
		width: 30px;
		position: absolute;
		top: 60px;
		left: 0;
		cursor: pointer;
		&-line{
			display: block;
			height: 4px;
			background: #fff;
			width: 100%;
			position: relative;
			& + .mobile-menu__icon-line{
				margin-top: 6px;
			}
		}
		&.active{
			.mobile-menu__icon-line{
				background: #f8c255;
			}
		}
	}
}