.steps {
	background: #fff;
	position: relative;
	&__list {
		list-style: none;
		padding: 0;
		margin: 0;
		.clearfix();
	}
	&__item {
		float: left;
		width: 100% / 4;
		.clearfix();
		padding: 25px 0 20px;
		position: relative;
		font-size: 0;
		&:first-child{
			cursor: pointer;
		}
		&:last-child {
			&:after {
				background: none;
			}
		}
	}
	&__item-arrow{
		background: url('../img/icons/big_arrow.svg');
		display: inline-block;
		width: 30px;
		height: 30px;
		position: absolute;
		top: 54%;
		right: -11px;
		margin-top: -15px;
	}
	&__img {
		position: absolute;
		left: 26px;
		top: 50%;
		margin-top: -20px;
		width: 40px;
		height: 40px;
		&_search {

		}
		&_cart {
			width: 50px;
			height: 50px;
			margin-top: -25px;
		}
		&_manager {
			width: 50px;
			height: 50px;
			margin-top: -25px;
		}
		&_rocket {
			width: 50px;
			height: 50px;
			margin-top: -25px;
		}
	}
	&__text {
		display: block;
		width: 100%;
		padding-left: 90px;
		margin: 0;
		font-size: 14px;
		line-height: 20px;
		opacity: .7;
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=70)";
		padding-right: 15px;
	}
	&__tablet-text{
		display: none;
		width: 100%;
		padding-left: 90px;
		margin: 0;
		font-size: 14px;
		line-height: 20px;
		opacity: .7;
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=70)";
		padding-right: 15px;
	}
	&__mobile{
		display: none;
		max-width: 300px;
		padding-left: 20px;
		margin: 0 auto;
		padding-top: 16px;
		padding-bottom: 26px;
		&-image{
			width: 50px;
			float: left;
			display: block;
			&>.order-popup__img_manager{
				font-size: 18px;
				line-height: 18px;
			}
		}
		&-text{
			padding-top: 10px;
			display: block;
			padding-left: 70px;
			width: 100%;
			font-size: 14px;
		}
	}
	&__mobile-phone{
		color: #334c5d;
		font-size: 18px;
		line-height: 18px;
	}
}

.no-svg .steps__item-arrow{
	background: url('../img/png-icons/icons/big_arrow.png');
}

@media @TabletLandscape{
	.steps {
		&__list {
		}
		&__item {
			padding: 25px 0 25px;
			&:last-child {
				&:after {
				}
			}
		}
		&__item-arrow{
			right: 0;
		}
		&__img {
			&_search {
			}
			&_cart {
			}
			&_manager {
			}
			&_rocket {
			}
		}
		&__text {
			display: none;
		}
		&__tablet-text{
			display: block;
			padding-left: 80px;
			padding-right: 25px;
		}
	}
}
@media @MobilePortrait{
	.steps__mobile{
		padding-left: 0;
		width: 280px;
	}
}