html{
	font-size: 16px;
}
body {
	.roboto();
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	line-height: 1.3;
	color: #333;
	left: 0;
	position: relative;
	@media @TabletLandscape{
		overflow-x: hidden;
	}
	
}
.wrapper{
	position: relative;
	width: 100%;
	overflow-x: hidden;
}
.site-container{
	position: relative;
	width: 100%;
	padding: 0;
	z-index: 2;
	.transition(margin .4s);
	&.slided{
		margin-left: 240px;
	}
}
.shadow-box{
	position: absolute;
	width: 0;
	height: 100%;
	top:0;
	left: 0;
	background: #fff;
	&:before{
		content: '';
		position: absolute;
		width: 0;
		height: 100%;
		top:0;
		left: 0;
		.box-shadow(0px 0px 38px 3px #000);
	}
	&:after{
		position: absolute;
		width: 50px;
		height: 100%;
		top:0;
		left: 0;
		content: '';
		background: #fff;
	}
}

// Selection color
::selection {
  background: @selection-color; /* WebKit/Blink Browsers */
}

::-moz-selection {
  background: @selection-color; /* Gecko Browsers */
}

a {
	text-decoration: none;
}

* {
	box-sizing: border-box;
}

a,
input,
select,
button,
textarea {
	&:focus {
		outline: none;
	}
}

h3{
	font-size: 18px;
	line-height: 20px;
	font-weight: 400;
}
h2{
	font-size: 24px;
	line-height: 1.3;
	font-weight: 300;
	color: #333;
}
.hide{
	display: none;
}

.container {
	width: 1180px;
	margin: 0 auto;
	.clearfix();
	@media @TabletLandscape{
		width: 882px;
	}
	@media @TabletPortrait{
		width: 100%;
		padding: 0 10px;
	}
	@media @MobilePortrait{
		padding: 0 20px;
	}
}

.content {
	background: #e9eae2;
	padding-top: 40px;
	position: relative;
	@media @MobilePortrait{
		padding-top: 20px;
	}
	[class^="content__column"] {
		padding: 0 (@gutter / 2);
		float: left;
	}
	&__row {
		.clearfix();
		margin-left:  (@gutter / -2);
		margin-right: (@gutter / -2);
		margin-bottom: @gutter;
		&> .content__right{
			float: right;
		}
	}
	&__row2col{
		margin-left:  (@gutter / -2);
		margin-right: (@gutter / -2);
		margin-bottom: @gutter;
	}
	&__col{
		padding: 0 (@gutter / 2);
		width: 50%;
		float: left;
		vertical-align: top;
		display: block;
	}
	&__column-1 {
		width: (100% / 4) * 1;
	}
	&__column-2 {
		width: (100% / 4) * 2;
		.perspective(10000px);
		position: relative;
		height: 350px;
	}
	&__column-3 {
		width: (100% / 4) * 3;
	}
	&__column-4 {
		width: (100% / 4) * 4;
	}
	
	&__center{
		display: block;
		width: 100%;
		padding-left: 25%;
		padding-right: 25%;
	}
}

.tb{
	&__show{
		display: none;
	}
	&__col-3{

	}
	&__col-2{

	}
	&__col-1{

	}
	&__left{

	}
}
@media @TabletLandscape{
	.tb{
		position: relative;
		.tb__row{
			font-size: 0;
			margin-bottom: 0;
			margin-right: 0;
			margin-left: -20px;
			position: relative;
			&>*{
				font-size: 16px;
			}
			& + .tb__row{
				margin-top: -371px;

			}
			&> .tb__left{
				float: left;
			}
			&>.tb__col-3{
				width: 100%;
				padding: 0;
				margin-bottom: 20px;
				padding-left: 20px;
				float: left;
			}
			&>.tb__col-2{
				width: 580px;
				padding: 0;
				margin-bottom: 20px;
				float: left;
				margin-left: 20px;
				float: left;
			}
			&>.tb__col-1{
				width: 280px;
				padding: 0;
				margin-bottom: 20px;
				margin-left: 20px;
				float: left;
			}
			&> .tb__footer-padding-line{
				margin-top: -10px;
				padding-top: 33px;
				border-top: solid 1px #2e4454;
				.clearfix();
				.site-footer__text {
					margin-bottom: 20px;
				}
				.tb__col-1{
					width: 280px;
					padding: 0;
					margin-bottom: 0;
					margin-left: 20px;
					float: left;
					border-bottom: none;
					text-align: left;
				}
			}
		}
		&__show{
			display: block;
		}
		&__hide{
			display: none;
		}
	}
}
@media @TabletPortrait{
	.product-page{
		.tb__col-2{
			text-align: left;
		}
	}
	.tb{
		width: 600px;
		margin: 0 auto;
		&__p_show{
			display: block !important;
		}
		&__p_hidden{
			display: none !important;
		}
		.tb__row{
			text-align: center;
			margin: 0;
			margin-right: -30px;
			margin-left: -30px;
			& + .tb__row{
				margin-top: 0px;
			}
			&>.tb__col-1{
				float: none;
				display: inline-block;
				vertical-align: top;
				text-align: left;
			}
			&> .tb__p-col-1{
				width: 280px;
				float: none;
				display: inline-block;
				vertical-align: top;
				text-align: left;
			}
			&> .tb__p-col-2{
				margin-left: 0;
				width: 580px;
				float: none;
				display: inline-block;
			}
			&> .tb__footer-padding-line{
				.tb__col-1{
					padding-top: 20px;
				}
			}
		}		
	}
}
@media @MobilePortrait{
	.tb{
		width: 100%;
		&__m-show{
			display: inline-block !important;
		}
		&__m-hide{
			display: none !important;
		}
		.tb__row{
			margin-left: 0;
			margin-right: 0;
			&>.tb__col-1{
				margin-left: 0;
			}
			&> .tb__p-col-1{
				margin-left: 0;
			}
			&> .tb__footer-padding-line{
				padding: 0;
				border: none;
				margin-top: 0;
				.clearfix();
				.site-footer__text {
					& + .site-footer__text {
						margin-bottom: 0;
					}
				}
				.tb__col-1{
					margin-left: 0;
				}
			}
			&> .tb__p-col-2{
				margin-left: 0;
				width: 280px;
			}
		}
	}
	.site-footer{
		.tb .tb__row{
			&> .tb__col-1{
				margin-bottom: 0;
			}
			&> .tb__footer-padding-line{
				.tb__col-1{
					padding: 25px 20px 0 0;
				}
			}
		}
	}
}

.responsive-video{
	position: relative;
	padding-bottom: (9/16)*100%;
	opacity: 0.5;
	.transition(opacity .4s);
	&:hover{
		opacity: 1;
	}
	&.activated{
		opacity: 1;
	}
	#player{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 1;
	}
}