.article{
	
}
.article__heading{
	background: #334C5D;
	color: #fff;
	font-weight: 500;
	min-height: 260px;
	overflow: hidden;
	position: relative;
}
.article__head_img{
	opacity: .5;
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
	position: absolute;
	z-index: 1;
	width: 100%;
	top: 0;
	left: 0;
}
.article__head_main{
	position: relative;
	z-index: 2;
	padding: 26px 30px;
}
.article__category{
	font-size: 14px;
	text-transform: uppercase;
	font-style: italic;
	margin-bottom: 70px;
}
.article__title{
	font-size: 26px;
	line-height: 30px;
	font-weight: 500;
	margin-bottom: 60px;
}
.article__text{
	background: #fff;
	padding: 36px 30px;
	font-size: 14px;
	line-height: 20px;
	p{
		margin-bottom: 20px;
	}
	p:first-child{
		margin-top: 0;
	}
	blockquote{
		margin: 36px 0;
		font-size: 1.4em;
		line-height: 1.1;
	}
}


.to_read{
	background: #fff;
	margin-bottom: 54px;
	@media @MobileLandscape{
		margin-bottom: 33px;
	}
}
.to_read__title{
	margin: 0;
	padding: 24px 30px;
}
.to_read__list{
	list-style: none;
	margin: 0;
	padding: 0;
}
.to_read__item{
	border-top: 1px solid #E5E5E5;
	&.active,
	&.active + &{
		border-color: #DFAE4C;
	}
}
.to_read__link{
	display: block;
	color: #333;
	padding: 24px 60px 24px 30px;
	font-size: 14px;
	line-height: 1.5;
	position: relative;
	.to_read__item.active &{
		background: #F8C255;
		color: #000;
	}
	.icon-chevron-right{
		background: url('../img/icons/big_arrow.svg') no-repeat;
		position: absolute;
		display: block;
		top: 50%;
		margin-top: -10px;
		right: 24px;
		width: 20px;
		height: 20px;
	}
}
.to_read__icon{}
.realted_product{
	&__title{
		margin-bottom: 34px;
	}
}

.article{
	.container{
		&>.content__row{
			margin-bottom: 80px;
		}
	}
}
@media @TabletPortrait{
	.article{
		&__aside-row{
			margin-left: 0 !important;
			margin-right: 0 !important;
			font-size: 0;
			letter-spacing: 0;
			&>.tb__col-1{
				float: left;
				margin-left: 0 !important;
				margin-right: 0 !important;
				& + .tb__col-1{
					float: right;

				}
			}
		}
	}
}
@media @MobileLandscape{
	.article{
		&__category{
			display: none;
		}
		&__head_img{
			display: none;
		}
		&__heading{
			background: #fff;
			color: #333;
		}
		&__title{
			letter-spacing: 0.01em;
			margin-bottom: 0;
		}
		&__head_main{
			padding: 8px 18px;
		}
		&__text {
			padding: 22px 19px 26px;
		}
	}
}
@media @MobilePortrait{
	.article{
		&__heading{
		}
		&__head_img{
		}
	}
}